import React, { Component, PropTypes } from 'react'
import _ from 'lodash'
import Icon from 'react-fontawesome'
import Textarea from 'xadmin-form/src/components/Textarea'
import { app, StoreWrap } from 'xadmin-core'

const HtmlPart = React.createClass({

  render() {
    const { html, params=[] } = this.props
    const vs = params.reduce(( prev, { key, value }) => {
      prev[key] = value
      return prev
    }, {})
    return <div dangerouslySetInnerHTML={{ __html: _.template(html)(vs) }} />
  }

})

HtmlPart.Title = 'HTML'

HtmlPart.ParamSchema = {
  type: 'object',
  properties: {
    html: {
      type: 'string',
      format: 'code'
    },
    params: {
      type: 'array',
      title: '',
      items: {
        type: 'object',
        properties: {
          key: {
            type: 'string',
            title: ''
          },
          value: {
            type: 'string',
            title: ''
          }
        }
      }
    }
  },
  form: [ '*', { key: 'html', component: Textarea }, 'params' ]
}

export default HtmlPart



